.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: rgb(15, 68, 68);
}
.items {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.items img {
  width: 350px;
  height: 400px;
}

.item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 5px;
  margin: 5px;
  box-shadow: 2px 2px 10px black;
}

button {
  display: inline-block;
  line-height: 3em;
  padding: 0 1em;
  background: #42b18c;
  border-radius: 0.125em;
  cursor: pointer;
  border: none;
  margin: 10px;
}
button:hover {
  color: #fff;
  background-color: rgb(22, 21, 21);
  box-shadow: 0 0.5em 0.5em -0.4em;
}

header {
  font-size: 20px;
  background-color: rgb(36, 163, 93);

  padding-top: 20px;
}

.title {
  color: rgb(74, 177, 74);
  font-size: bold;
}
